.main-header {
  width: 100%;
  height: 50px;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 33px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.main-header .searchbar-wrap {
  border: none;
  display: none;
}

.main-header .searchbar-wrap .searchbar-content {
  flex-direction: row;
  display: flex;
}

.main-header .searchbar-wrap .searchbar-content input {
  -o-transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  background-color: #fff;
  border: 2px solid #cfd4db;
  flex: 1;
  margin-right: 10px;
  padding: 8px 14px;
  font-size: 14px;
  transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  display: block;
}

.main-header .searchbar-wrap .searchbar-content input:focus {
  background-color: #cfd4db;
  border-color: #b1b8c4;
  outline: none;
}

.main-header .searchbar-wrap .searchbar-content .button {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .05em;
  -o-transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  background-color: #e91e63;
  border: 0;
  flex: 1;
  padding: 8px 14px;
  font-size: 12px;
  transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  display: block;
}

.main-header .searchbar-wrap .searchbar-content .button:hover, .main-header .searchbar-wrap .searchbar-content .button:active, .main-header .searchbar-wrap .searchbar-content .button:focus {
  background-color: #c2185b;
  outline: none;
}

.main-header .pixijs-logo {
  width: 85px;
  height: 33px;
  vertical-align: text-bottom;
  background: url("logo.baca55d3.svg") center / 100% no-repeat;
  display: inline-block;
}

.main-header .version {
  color: #627189;
  font-size: 14px;
  display: inline-block;
}

.main-header .hamburger {
  width: 30px;
  height: 30px;
  display: inline-block;
  position: absolute;
  top: 11px;
  right: 15px;
}

.main-header .hamburger i {
  width: 22px;
  height: 2px;
  transform-origin: 50%;
  background: #e91e63;
  margin: -2px 0 0 -11px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.main-header .hamburger i#line1 {
  transform: translateY(-8px);
}

.main-header .hamburger i#line3 {
  transform: translateY(8px);
}

.main-nav {
  width: 250px;
  z-index: 2;
  -o-transition: left .2s cubic-bezier(.215, .61, .355, 1);
  -webkit-overflow-scrolling: touch;
  background: #38404e;
  padding: 25px 0 50px;
  transition: left .2s cubic-bezier(.215, .61, .355, 1);
  position: fixed;
  top: 50px;
  bottom: 0;
  left: -250px;
  overflow: auto;
}

.main-nav.mobile-open {
  left: 0;
}

.main-nav .main-menu .section {
  width: 100%;
  height: 32px;
  color: #b1b8c4;
  text-transform: uppercase;
  letter-spacing: .05em;
  cursor: pointer;
  background: url("arrow-right-default.ba382202.png") 15px 12px no-repeat;
  padding: 9px 30px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  display: block;
}

.main-nav .main-menu .section:hover, .main-nav .main-menu .section.open {
  color: #fff;
  -o-transition: color .2s cubic-bezier(.215, .61, .355, 1);
  transition: color .2s cubic-bezier(.215, .61, .355, 1);
}

.main-nav .main-menu .section.open {
  background: url("arrow-down-default.fea45570.png") 14px 13px no-repeat;
}

.main-nav .main-menu ul {
  margin: 0;
  padding: 5px 0 10px;
  list-style: none;
  display: none;
}

.main-nav .main-menu ul li {
  width: 100%;
  height: 32px;
  cursor: pointer;
  -o-transition: all .2s cubic-bezier(.215, .61, .355, 1);
  padding: 9px 30px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  transition: all .2s cubic-bezier(.215, .61, .355, 1);
  display: block;
  box-shadow: inset 0 0 #ec407a;
}

.main-nav .main-menu ul li.invalid {
  color: #6c747f;
}

.main-nav .main-menu ul li:hover {
  color: #fff;
}

.main-nav .main-menu ul li.selected {
  color: #fff;
  -o-transition: all .2s cubic-bezier(.215, .61, .355, 1);
  transition: all .2s cubic-bezier(.215, .61, .355, 1);
  box-shadow: inset 2px 0 #ec407a;
}

.main-nav .about {
  margin-top: 20px;
  padding-left: 17px;
}

.main-nav .about .goodboy-logo {
  width: 99px;
  height: 23px;
  background: url("goodboy-logo.e357ffea.png") 0 0 / 99px 23px;
  margin-bottom: 12px;
  display: block;
}

.main-nav .about .goodboy-logo img {
  width: 100%;
  height: 100%;
  display: block;
}

.main-content {
  z-index: 1;
  -webkit-overflow-scrolling: touch;
  background: #272d37;
  padding: 0 10px;
  position: fixed;
  inset: 50px 0 0;
  overflow: auto;
}

body.normal .main-content {
  -o-transition: left .2s cubic-bezier(.215, .61, .355, 1);
  transition: left .2s cubic-bezier(.215, .61, .355, 1);
}

.main-content .main-tabs, .main-content .redirect {
  display: none;
}

.main-content .example-frame {
  width: 100%;
  max-width: 800px;
  background: #38404e url("tile.664357af.png") 56px 56px;
  position: relative;
}

.main-content .example-frame .sizer {
  width: 100%;
  padding-top: 75%;
  position: relative;
}

.main-content .example-frame .sizer iframe {
  width: 100%;
  height: 100%;
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.main-content .example-frame .reload {
  width: 64px;
  height: 64px;
  z-index: 2;
  cursor: pointer;
  margin: -26px 0 0 -32px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate3d(0, 0, 0);
}

.main-content .example-frame .reload svg {
  width: 24px;
  height: 24px;
  z-index: 3;
  -o-transition: all .2s cubic-bezier(.215, .61, .355, 1);
  margin: -10px 0 0 -10px;
  transition: all .2s cubic-bezier(.215, .61, .355, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(0);
}

.main-content .example-frame .reload svg path {
  fill: #ec407a;
  -o-transition: fill .2s cubic-bezier(.215, .61, .355, 1);
  transition: fill .2s cubic-bezier(.215, .61, .355, 1);
}

.main-content .example-frame .reload:hover svg {
  transform: rotate(180deg);
}

.main-content .example-frame .reload:hover svg path {
  fill: #d81b60;
}

.main-content .example-frame .reload:before {
  width: 100%;
  height: 100%;
  z-index: 2;
  content: "";
  background: #272d37;
  border: 2px solid #919bac;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.main-content .example-frame .reload:after {
  width: 100%;
  height: 100%;
  z-index: 1;
  content: "";
  background: none;
  border: 2px solid #919bac;
  display: block;
  position: absolute;
  top: 6px;
  left: 5px;
}

.main-content .code {
  width: 100%;
  max-width: 800px;
  height: auto;
  border: 1px solid #465162;
  margin-top: 75px;
}

.main-content .code textarea {
  resize: none;
  height: 0;
  background: none;
  border: 0;
  overflow: hidden;
}

.main-content .code h3 {
  height: 50px;
  color: #919bac;
  border-bottom: 1px solid #465162;
  margin: 0;
  padding: 17px 0 0 22px;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
}

.main-content .code .CodeMirror {
  letter-spacing: .05em;
  color: #fff;
  height: auto;
  background: #272d37 url("code-lines.044d1c9f.png") 0 5px;
  font-family: Roboto Mono;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.main-content .code .CodeMirror .cm-keyword, .main-content .code .CodeMirror .cm-string, .main-content .code .CodeMirror .cm-atom, .main-content .code .CodeMirror .cm-variable-2 {
  color: #f48fb1;
}

.main-content .code .CodeMirror .cm-def, .main-content .code .CodeMirror .cm-number {
  color: #cfd4db;
}

.main-content .code .CodeMirror .cm-comment {
  color: #919bac;
}

.main-content .code .CodeMirror pre {
  padding: 0 4px 0 14px;
}

.main-content .code .CodeMirror-gutters {
  background: #272d37;
  border-right: 1px solid #465162;
}

.main-content .code .CodeMirror-linenumber {
  color: #919bac;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.main-content .code .CodeMirror-scroll {
  height: auto;
  margin: 0 0 -30px;
  overflow-x: auto;
  overflow-y: hidden;
}

.main-content .code .footer {
  text-align: center;
  background: #38404e;
  border-top: 1px solid #465162;
  padding: 8px 30px 18px 12px;
}

.main-content .code .footer .select-group {
  text-align: left;
  margin: 10px 18px 0;
  display: inline-block;
}

.main-content .code .footer .select-group .label, .main-content .code .footer .select-group .select {
  vertical-align: middle;
  display: inline-block;
}

.main-content .code .footer .select-group .label {
  color: #919bac;
  margin: 10px 15px 10px 0;
  font-size: 14px;
  line-height: 14px;
}

.main-content .code .footer .select-group .select {
  height: 40px;
  width: 175px;
  z-index: 10;
  background: url("icon-arrow-down.bb783765.png") 155px 18px no-repeat;
  position: relative;
}

.main-content .code .footer .select-group .select.open {
  background-image: url("icon-arrow-up.cf798348.png");
}

.main-content .code .footer .select-group .select .current {
  width: 100%;
  height: 100%;
  color: #fff;
  border: 2px solid #919bac;
  padding: 13px 0 0 13px;
  font-size: 12px;
  line-height: 12px;
  display: block;
}

.main-content .code .footer .select-group .select ul {
  width: 100%;
  background: #38404e;
  border: 2px solid #919bac;
  border-bottom: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  position: absolute;
  bottom: 100%;
  left: 0;
}

.main-content .code .footer .select-group .select ul li {
  width: 100%;
  height: 32px;
  color: #fff;
  -o-transition: all .2s cubic-bezier(.215, .61, .355, 1);
  cursor: pointer;
  padding: 10px 13px 0;
  font-size: 12px;
  line-height: 12px;
  transition: all .2s cubic-bezier(.215, .61, .355, 1);
  display: block;
}

.main-content .code .footer .select-group .select ul li:hover, .main-content .code .footer .select-group .select ul li.selected {
  color: #f06292;
  background: #272d37;
}

.main-content .code .footer .button {
  color: #fff;
  text-transform: uppercase;
  height: 40px;
  letter-spacing: .05em;
  -o-transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  background: #e91e63;
  border: 0;
  margin-top: 10px;
  padding-left: 25px;
  padding-right: 25px;
  font-size: 12px;
  transition: background-color .2s cubic-bezier(.215, .61, .355, 1);
  display: inline-block;
}

.main-content .code .footer .button:hover, .main-content .code .footer .button:active, .main-content .code .footer .button:focus {
  background-color: #c2185b;
  outline: none;
}

.main-content .submit {
  color: #919bac;
  margin: 30px 0 40px;
  font-size: 16px;
  font-weight: 300;
}

.main-content .submit a {
  color: #fff;
}

.main-content .submit a:hover {
  color: #ec407a;
}

