body.embed .content-controls .submit, body.embed .content-controls .footer, body.embed #example-title, body.embed .main-header, body.embed .main-nav, body.embed .code-header, body.embed.nocode .content-controls, body.embed.nocode .main-tabs, body.embed.autoplay .reload, body.embed.noredirect .redirect {
